@import 'ScssHelpers/helpers-all';

.pet_food {
	.noUi-marker.noUi-marker-horizontal.noUi-marker-normal {
		background: none;
	}

	.noUi-horizontal {
		height: 1.8rem;
	}

	.noUi-target {
		box-shadow: none;
		border-radius: 1.3rem;
	}

	.noUi-connect {
		background: #bb753a;
		border-radius: 1.3rem;
		height: 1.8rem;
		top: 50%;
		margin-top: -.9rem;
	}

	.noUi-connects {
		overflow: hidden!important;
		background: #f0f2f4;
		border-radius: 1.3rem;
		overflow: visible;
		height: 1.8rem;
	}

	.noUi-value {
		color: $black;

		@include bp($point_4, min) {
			font-size: 2.2rem;
		}
		@include bp($point_4 - 1) {
			font-size: 1.8rem;
		}
	}

	.noUi-marker-horizontal.noUi-marker-sub {
		height: 1rem;
	}

	.noUi-handle.noUi-handle-lower {
		top: -.7rem;
		width: 3.2rem;
		height: 3.2rem;

		border-radius: 50%;
		background: #bb753a;
		box-shadow: none;
		border: none;

		&:before,
		&:after {
			display: none;
		}
	}

	.noUi-pips-horizontal {
		padding: 2.4rem 0 0;
	}

	.noUi-marker-horizontal.noUi-marker-large {
		height: 1rem;
	}

	.noUi-marker-horizontal {
		background: #d0d0d0;
	}
}

.pet_food__slider_w {
	border-radius: .8rem;
	border: 1px solid #f2e4d1;

	&:not(:last-child) {
		margin-bottom: 4rem;
	}

	@include bp($point_4, min) {
		padding: 3.2rem 10.4rem 12.6rem;
	}
	@include bp($point_4 - 1, $point_6) {
		padding: 3rem 4rem 12rem;
	}
	@include bp($point_6 - 1) {
		padding: 3rem 2.6rem 12rem;
	}
}

.pet_food__slider_title {
	@include h3;

	text-align: center;
}

.pet_food__slider_title_w {
	@include flex_block(row, wrap, center, center, center);

	margin-bottom: 2.8rem;
}

.pet_food__slider_input_title {
	@include h3;

	font-family: $font_1;
	font-weight: 600;
	letter-spacing: -.02em;
	text-align: center;
	border: none;
}
